<template>
  <!-- 权限 -->
  <div class="main_con_box">
    <div class="power_top_btn">
      <el-button type="primary" size="small" @click="onSubmit()"
        >新建</el-button
      >
    </div>
    <div class="power_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        size="mini"
        :height="$store.state.innerheight - 165"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column fixed type="index" label="序号" width="80">
        </el-table-column>
        <el-table-column prop="menu_name" label="菜单名称" width="">
        </el-table-column>
        <el-table-column prop="permission_name" label="权限名称" width="">
        </el-table-column>
        <el-table-column prop="identification" label="标识" width="">
        </el-table-column>
        <el-table-column prop="type" label="类型" width=""> </el-table-column>
        <el-table-column prop="remarks" label="备注" width="">
        </el-table-column>
        <el-table-column label="操作" width="">
          <template #default="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="mini"
              >编辑</el-button
            >
            <el-button
              style="color: red"
              type="text"
              size="mini"
              @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="page">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    <div>
      <powerDetail ref="powerDetail"></powerDetail>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import powerDetail from "../power/power_details.vue";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  components: {
    powerDetail,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      tableData: [
        {
          menu_name: "1",
          permission_name: "1",
          identification: "1",
          type: "1",
          remarks: "1",
        },
        {
          menu_name: "1",
          permission_name: "1",
          identification: "1",
          type: "1",
          remarks: "1",
        },
        {
          menu_name: "1",
          permission_name: "1",
          identification: "1",
          type: "1",
          remarks: "1",
        },
      ],
    });
    const onSubmit = () => {
      console.log(proxy.$refs.powerDetail);
      proxy.$refs.powerDetail.setshow();
    };
    const handleEdit = (res) => {
      proxy.$refs.powerDetail.setedit(res);
    };
    const handleDel = () => {
      console.log();
    };
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    return {
      ...toRefs(state),
      onSubmit,
      handleEdit,
      handleDel,
      handleSizeChange,
      handleCurrentChange
    };
  },
};
</script>

<style scoped>
.main_con_box {
  background-color: #fff;
  padding: 10px;
  box-sizing: border-box;
}
.power_top_btn {
  text-align: right;
}
.power_main {
  margin-top: 10px;
}
</style>
